@import "./src/assets/sass/prepends.scss";

.card {
  margin-bottom: 0.5rem;
}
.others-content {
  margin: 0.75rem 0;

  > div {
    display: block;
    width: 100%;

    > * {
      display: inline-block;
    }
    > a {
      float: left;
    }
    > time {
      float: right;
      margin-top: 0.25rem;
    }
  }
  > p {
    clear: both;
    display: block;
    width: 100%;
  }
}
